import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityHomeworkItemComponent } from './activity-homework-item.component';
import { ActivityHomeworkItemStudentComponent } from './activity-homework-item-student/activity-homework-item-student.component';


const routes: Routes = [
  { path: '', component: ActivityHomeworkItemComponent,
    children: [
      { path: 'student/:studentid', component: ActivityHomeworkItemStudentComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityHomeworkItemRoutingModule { }
