import { Component, OnInit, ViewChild,TemplateRef, HostListener, ViewContainerRef, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { MatDialog } from '@angular/material/dialog';

import { ActivityHomeworkItemComponent } from 'src/app/modules/admin/activity/activity-homework/activity-homework-item/activity-homework-item.component';

import { ActivityHomeworkService } from 'src/app/modules/admin/activity/activity-homework/activity-homework.service';

import * as moment from 'moment';

@Component({
  selector: 'app-activity-homework-item-student',
  templateUrl: './activity-homework-item-student.component.html',
  styleUrls: ['./activity-homework-item-student.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ActivityHomeworkItemStudentComponent implements OnInit {

  student:any = {}

  activity:any = {}

  @ViewChild("submitHomeWorkRef", { static: false }) submitHomeWorkRef: TemplateRef<any>;

  constructor(
    private _overlay: Overlay,
    private _viewContainerRef: ViewContainerRef,
    public dialog: MatDialog,
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private _activityHomeworkItemComponent: ActivityHomeworkItemComponent,
    private activityHomeworkService: ActivityHomeworkService
  ) { }

  goBack() {
    this._activityHomeworkItemComponent.matDrawer.close();
    this.activityHomeworkService.setHomeWorkActivityStudentData(null)
    this._router.navigateByUrl('/activity/homework/list/details/'+this.activity.id);
  }

  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  openSubmitModel() {

    this.dialog.open(this.submitHomeWorkRef, {
      panelClass: 'material-dialog-medium'
    });

  }

  submitStudentActivityForm() {

    this.dialog.closeAll()

  }

  ngOnInit(): void {

    this.activityHomeworkService.homeworkactivitystudentdatacast.subscribe((res:any) => {
      if(res != null){
        this._activityHomeworkItemComponent.matDrawer.open();
        this.student = res.data
        console.log(res);
      }
    })

    this.activityHomeworkService.homeworkactivitydatacast.subscribe((res:any) => {
      if(res != null){
        this.activity = res
      }
    })

  }

}
